* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
   scroll-behavior: smooth;
}

a {
    color: #fff;
    text-decoration: none;
    border: none;
    display: inline-block;
}

.img-responsive {
    max-width: 100%;
    height: auto;
}

.body {
    font-family: Inter,Arial,Helvetica,sans-serif;
    background-color: #141414;
    color: #fff;
    font-size: 14px;
    line-height: 17px;
    font-weight: 400;
}

.container {
    max-width: 1200px;
    min-width: 375px;
    width: 100%;
    margin: 0 auto;
    padding: 0 30px;

    @media screen and (max-width: 768px) {
        padding: 0 15px;
    }
}

.btn {
    border: none;
    background-color: red;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 24px;
    font-weight: 900;
    transition: color .3s ease, background-color .3s ease;
    border-radius: 6px;
    padding: 6px 12px;
    cursor: pointer;

    &:hover {
        color: red;
        background-color: #fff;
    }

    &--white {
        color: #141414;
        background-color: #fff;

        &:hover {
            color: #fff;
            background-color: red;
        }
    }
}


.header {
    padding: 40px 0;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media screen and (max-width: 1024px) {
            flex-wrap: wrap;
        }

    }

    &__menu {
        display: flex;
        gap: 15px;

        @media screen and (max-width: 1024px) {
            order: 2;
            flex-basis: 100%;
            padding-top: 20px;
            gap: 10px;
        }

        &-link {
            text-transform: uppercase;
            font-size: 16px;
            line-height: 24px;
            font-weight: 900;
            transition: color .3s ease;

            @media screen and (max-width: 768px) {
                font-size: 14px;
            }

            &:hover {
                color: red;
            }
        }
    }

    &__btns {
        display: flex;
        gap: 16px;

        @media screen and (max-width: 768px) {
            order: 2;
            flex-basis: 100%;
            padding-top: 20px;
        }
    }
}

main  {
    margin-top: 60px;
}

section {
    margin: 40px 0;
    padding-bottom: 20px;
    border-bottom: 2px solid #fff;

    h1, h2 {
        margin-bottom: 30px;
    }

    h3 {
        margin: 20px 0;
    }

    ul {
        padding-left: 1rem;
    }

    p {
        margin: 10px 0;
    }
}

.banner {
    position: relative;
    padding: 40px 0;

    img {
        width: 100%;
    }

    h2 {
        position: absolute;
        top: 40%;
        left: 100px;
        font-size: 28px;
        font-weight: 900;
        @media screen and (max-width: 768px) {
            left: 40px;
            font-size: 18px;
        }
    }

    button {
        position: absolute;
        bottom: 30%;
        left: 100px;
        @media screen and (max-width: 768px) {
            left: 40px;
        }
    }
}

.site-menu {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    a {
        flex-basis: 50%;
        padding: 10px 0;
        transition: color .3s ease-in-out;
        &:hover {
            color: red
        }
    }
    &.hidden {
        display: none;
    }
    &-btn {
        display: flex;
        align-items: center;
        cursor: pointer;
        & .chevron {
            transition: transform .3s ease;
            &.active {
                transform: rotate(-180deg);
            }
        }
    }
}
.accordion {
    border: 1px solid #ccc;
    border-radius: 4px;

    &-arrow {
        transition: transform .3s ease;
        &.active {
            transform: rotate(-180deg);
        }
    }

    &-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #f1f1f1;
        color: #141414;
        cursor: pointer;
        padding: 10px;
        margin: 0;
        font-size: 18px;
        border-bottom: 1px solid #ccc;
    }

    &-content {
        padding: 10px;
        display: none;
        border-bottom: 1px solid #ccc;
        &:last-child {
            border-bottom: none;
        }
    }
}

.footer {
    nav {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        & a {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-basis: 25%;
            margin-bottom: 20px;
            @media screen and (max-width: 768px) {
                flex-basis: 50%;
            }

            img {
                width: 90px;
                height: 40px;
            }
        }
    }
}

.copy {
    text-align: center;
    color: #cccccc;
    margin-bottom: 20px;
}
